import React from 'react';

import Cards from '../Cards/Cards';

const Column = props => {
  const deleteColumn = () => {
    props.cards.forEach(cardId => props.onDeleteCard(props.columnId, cardId));
    props.onDeleteColumn(props.columnId);
  };

  return (
    <div className="flex-no-shrink w-72 mx-2 p-2 bg-white rounded shadow">
      <div className="flex justify-between items-center">
        <input
          type="text"
          className="font-bold text-grey-darkest mb-2"
          value={props.title}
          onChange={event => { props.onRenameColumn(props.columnId, event.target.value) }}
        />
        <button
          className="p-2 text-xs text-red-dark"
          onClick={deleteColumn}
        >
          Delete
      </button>
      </div>

      <Cards cards={props.cards} card={props.card} onUpdateCard={cardId => props.onNewCard(props.columnId, cardId)} />

      <button
        className="text-xs text-grey p-2 mt-2 -ml-2"
        onClick={() => props.onNewCard(props.columnId)}
      >
        Add a card...
    </button>
    </div>
  );
}

Column.defaultProps = {
  onNewCard: () => { },
  onDeleteColumn: () => { },
  onRenameColumn: () => { },
  onDeleteCard: () => { }
};

export default Column;



