import { BOARD_ACTIONS } from '../../actions/boardActions';

const initialState = {
  title: '',
  columns: []
};

const boardReducer = (state = initialState, action) => {
  switch (action.type) {
    case BOARD_ACTIONS.ADD_COLUMN: {
      return {
        ...state,
        columns: [...state.columns, action.payload.columnId]
      };
    }
    case BOARD_ACTIONS.DELETE_COLUMN: {
      const nextColumns = [...state.columns];
      nextColumns.splice(nextColumns.indexOf(action.payload.columnId), 1)

      return {
        ...state,
        columns: nextColumns
      }
    }
    default:
      return state;
  }
};

export default boardReducer;



