import { BOARD_ACTIONS } from '../../actions/boardActions';

const initialState = {
  cardId: null,
  description: '',
  type: '',
  storyPoint: '',
  creationDate: null
};

const cardReducer = (state = initialState, action) => {
  switch (action.type) {
    case BOARD_ACTIONS.ADD_CARD:
    case BOARD_ACTIONS.UPDATE_CARD: {
      const creationDate = action.type === BOARD_ACTIONS.ADD_CARD ? Date.now() : state.creationDate;

      return {
        ...state,
        cardId: action.payload.cardId,
        description: action.payload.description,
        type: action.payload.type,
        storyPoint: action.payload.storyPoint,
        creationDate
      };
    }
    case BOARD_ACTIONS.UPDATE_CARD_DESCRIPTION: {
      return {
        ...state,
        description: action.payload.description
      };
    }
    default:
      return state;
  }
};

export default cardReducer;



