import React, { Component } from 'react';
import { Provider } from 'react-redux';

import store from '../store/store';

import Board from '../pages/Board/Board';

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Board />
      </Provider>
    );
  }
}

export default App;



