import React from 'react';

const NewColumnButton = props => (
  <button
    className="flex-no-shrink w-72 mx-2 p-2 bg-grey rounded hover:shadow text-grey-darker text-center text-sm"
    onClick={props.onNewColumn}
  >
    Add a list...
  </button>
);

NewColumnButton.defaultProps = {
  onNewColumn: () => { }
};

export default NewColumnButton;



