import React from 'react';

const Card = props => (
  <div
    className="w-full p-4 bg-grey-lighter rounded mb-2 text-left"
  >
    <div className="flex justify-end">
      <button
        className="text-xs text-blue"
        onClick={() => props.onUpdateCard(props.cardId)}
      >
        View Card
    </button>
    </div>

    <textarea
      className="mb-2 bg-transparent w-full"
      value={props.description}
      onChange={event => props.onDescriptionUpdate(props.cardId, event.target.value)}
    />

    <div className="font-bold mr-2 text-grey-dark text-xs">{props.type}</div>

    <div className="flex text-grey-dark text-xs">
      <div className="mr-4">
        <span className="font-bold">Story Point: </span>
        <span>{props.storyPoint}</span>
      </div>
      <div>
        <span className="font-bold">Date: </span>
        <span>{new Date(props.creationDate).toLocaleDateString('en-US')}</span>
      </div>
    </div>
  </div>
);

Card.defaultProps = {
  onUpdateCard: () => { },
  onDescriptionUpdate: () => { }
};

export default Card;



