import { BOARD_ACTIONS } from '../../actions/boardActions';
import cardReducer from '../cardReducer/cardReducer';

const initialState = {};

const cardsReducer = (state = initialState, action) => {
  switch (action.type) {
    case BOARD_ACTIONS.ADD_CARD:
    case BOARD_ACTIONS.UPDATE_CARD:
    case BOARD_ACTIONS.UPDATE_CARD_DESCRIPTION: {
      return {
        ...state,
        [action.payload.cardId]: cardReducer(state[action.payload.cardId], action)
      };
    }
    case BOARD_ACTIONS.DELETE_CARD: {
      const nextState = { ...state };
      delete nextState[action.payload.cardId];

      return nextState;
    }
    default:
      return state;
  }
}

export default cardsReducer;



