import { BOARD_ACTIONS } from '../../actions/boardActions';
import columnReducer from '../columnReducer/columnReducer';

const initialState = {};

const columnsReducer = (state = initialState, action) => {
  switch (action.type) {
    case BOARD_ACTIONS.ADD_COLUMN:
    case BOARD_ACTIONS.RENAME_COLUMN:
    case BOARD_ACTIONS.ADD_CARD:
    case BOARD_ACTIONS.DELETE_CARD: {
      return {
        ...state,
        [action.payload.columnId]: columnReducer(state[action.payload.columnId], action)
      };
    }
    case BOARD_ACTIONS.DELETE_COLUMN: {
      const nextState = { ...state };
      delete nextState[action.payload.columnId];

      return nextState;
    }
    case BOARD_ACTIONS.MOVE_CARD: {
      return {
        ...state,
        [action.payload.sourceColumnId]: columnReducer(state[action.payload.sourceColumnId], action),
        [action.payload.destinationColumnId]: columnReducer(state[action.payload.destinationColumnId], action)
      };
    }
    default:
      return state;
  }
};

export default columnsReducer;



