import nanoid from 'nanoid';

const ID_LENGTH = 10;

export const BOARD_ACTIONS = {
  ADD_COLUMN: 'ADD_COLUMN',
  DELETE_COLUMN: 'DELETE_COLUMN',
  RENAME_COLUMN: 'RENAME_COLUMN',
  ADD_CARD: 'ADD_CARD',
  DELETE_CARD: 'DELETE_CARD',
  UPDATE_CARD: 'UPDATE_CARD',
  MOVE_CARD: 'MOVE_CARD',
  UPDATE_CARD_DESCRIPTION: 'UPDATE_CARD_DESCRIPTION'
};

export const addColumn = () => ({
  type: BOARD_ACTIONS.ADD_COLUMN,
  payload: {
    columnId: nanoid(ID_LENGTH)
  }
});

export const deleteColumn = columnId => ({
  type: BOARD_ACTIONS.DELETE_COLUMN,
  payload: { columnId }
});

export const renameColumn = (columnId, title) => ({
  type: BOARD_ACTIONS.RENAME_COLUMN,
  payload: {
    columnId,
    title
  }
})

export const addCard = (columnId, values) => ({
  type: BOARD_ACTIONS.ADD_CARD,
  payload: {
    columnId,
    cardId: nanoid(ID_LENGTH),
    description: values.description,
    type: values.type,
    storyPoint: values.storyPoint
  }
});

export const deleteCard = (columnId, cardId) => ({
  type: BOARD_ACTIONS.DELETE_CARD,
  payload: {
    columnId,
    cardId
  }
});


export const updateCard = (values) => ({
  type: BOARD_ACTIONS.UPDATE_CARD,
  payload: {
    cardId: values.cardId,
    description: values.description,
    type: values.type,
    storyPoint: values.storyPoint
  }
});

export const moveCard = result => ({
  type: BOARD_ACTIONS.MOVE_CARD,
  payload: result
});

export const updateCardDescription = (cardId, description) => ({
  type: BOARD_ACTIONS.UPDATE_CARD_DESCRIPTION,
  payload: {
    cardId,
    description
  }
});



