import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';

import Page from '../../components/Page/Page';
import Columns from '../../components/Columns/Columns';
import ColumnContainer from './ColumnContainer';
import CardContainer from './CardContainer';
import CardDetailContainer from './CardDetailContainer';

import {
  addColumn,
  deleteColumn,
  renameColumn,
  addCard,
  deleteCard,
  updateCard,
  moveCard
} from '../../store/actions/boardActions';

const DroppableColumn = props => (
  <Droppable droppableId={props.columnId}>
    {(provided, snapshot) => (
      <div
        className={snapshot.isDraggingOver ? 'opacity-50' : ''}
        ref={provided.innerRef}
        {...provided.droppableProps}
      >
        <ColumnContainer {...props} />
        {provided.placeholder}
      </div>
    )}
  </Droppable>
);

const DraggableCard = props => (
  <Draggable draggableId={props.cardId} index={props.index}>
    {(provided, snapshot) => (
      <div
        className={snapshot.isDragging ? 'shadow' : ''}
        ref={provided.innerRef}
        {...provided.draggableProps}
        {...provided.dragHandleProps}
      >
        <CardContainer {...props} />
      </div>
    )}
  </Draggable>
);

class Board extends Component {
  state = {
    columnId: null,
    cardId: null,
  };

  openCardDetail = (columnId, cardId) => this.setState({ columnId, cardId });

  closeCardDetail = () => this.setState({ columnId: null, cardId: null });

  addCard = values => {
    if (values.cardId) {
      this.props.updateCard(values);
    } else {
      this.props.addCard(this.state.columnId, values);
    }

    this.closeCardDetail();
  }

  deleteCard = (columnId, cardId) => {
    this.props.deleteCard(columnId, cardId);

    this.closeCardDetail();
  }

  handleCardDrop = result => {
    const { draggableId, source, destination, reason } = result;

    if (
      reason === 'DROP' &&
      destination &&
      !(destination.droppableId === source.droppableId && destination.index === source.index)
    ) {
      this.props.moveCard({
        cardId: draggableId,
        sourceColumnId: source.droppableId,
        sourceIndex: source.index,
        destinationColumnId: destination.droppableId,
        destinationIndex: destination.index
      });
    }
  }

  render() {
    return (
      <Page>
        <DragDropContext onDragEnd={this.handleCardDrop}>
          <Columns
            columns={this.props.columns}
            column={DroppableColumn}
            card={DraggableCard}
            onNewColumn={this.props.addColumn}
            onNewCard={this.openCardDetail}
          />
        </DragDropContext>
        {
          !!this.state.columnId &&
          <CardDetailContainer
            onClose={this.closeCardDetail}
            onDeleteCard={this.deleteCard}
            onAddCard={this.addCard}
            columnId={this.state.columnId}
            cardId={this.state.cardId}
          />
        }
      </Page>
    );
  }
}

const mapStateToProps = state => ({
  columns: state.board.columns
});

const mapDispatchToProps = dispatch => bindActionCreators({
  addColumn,
  deleteColumn,
  renameColumn,
  addCard,
  deleteCard,
  updateCard,
  moveCard
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Board);



