import React from 'react';

const Cards = props => {
  const Card = props.card;

  return (
    <div>
      {props.cards.map((cardId, index) => (
        <Card
          key={cardId}
          cardId={cardId}
          onUpdateCard={props.onUpdateCard}
          index={index}
        />
      ))}
    </div>
  );
}

Cards.defaultProps = {
  cards: []
};

export default Cards;



