import React from 'react';
import { Formik, Form, Field } from 'formik';

const TYPES = [
  'New Feature',
  'Improvement',
  'Duplicate',
  'Refactor',
  'Bug'
];

const CardForm = props => (
  <Form>
    <div className="mb-4">
      <label htmlFor="description" className="block text-grey-darker text-sm font-bold mb-2">
        Description
      </label>
      <Field
        component="textarea"
        id="description"
        name="description"
        rows={3}
        className="border rounded w-full p-3"
      />
    </div>
    <div className="mb-4">
      <label htmlFor="type" className="block text-grey-darker text-sm font-bold mb-2">
        Type
      </label>
      <Field component="select" id="type" name="type" className="border p-3 rounded w-full">
        {TYPES.map(type => (<option key={type}>{type}</option>))}
      </Field>
    </div>
    <div className="mb-4">
      <label htmlFor="story-point" className="block text-grey-darker text-sm font-bold mb-2">
        Story Points
      </label>
      <Field type="text" id="story-point" name="storyPoint" className="border p-3 rounded w-1/3" />
    </div>
    <div className="flex justify-between mt-8">
      <button type="submit" className="px-2 py-1 rounded bg-blue text-white hover:shadow">
        {props.values.cardId ? 'Update card' : 'Add card'}
      </button>
      <button
        type="button"
        className="px-2 py-1 rounded bg-red-dark text-white hover:shadow"
        onClick={props.onCancel}
      >
        Cancel
      </button>

      {
        props.values.cardId && <button
          type="button"
          className="px-2 py-1 rounded bg-red-dark text-white hover:shadow"
          onClick={() => props.onDeleteCard(props.values.cardId)}
        >
          Delete card
        </button>
      }
    </div>
  </Form>
);

CardForm.defaultProps = {
  onCancel: () => { }
};

const initialValues = {
  description: '',
  type: TYPES[0],
  storyPoint: ''
}

const CardDetail = props => {
  return (
    <div className="w-full h-full bg-black-transparent absolute pin">
      <div className="w-72 bg-white rounded m-auto mt-32 p-4">
        <Formik
          initialValues={props.initialValues || initialValues}
          onSubmit={props.onAddCard}
          render={formProps => (
            <CardForm
              {...formProps}
              onDeleteCard={cardId => props.onDeleteCard(props.columnId, cardId)}
              onCancel={props.onClose}
            />
          )}
        />
      </div>
    </div>
  );
}

CardDetail.defaultProps = {
  onDeleteCard: () => { },
  onAddCard: () => { }
};

export default CardDetail;



