import { BOARD_ACTIONS } from '../../actions/boardActions';

const initialState = {
  columnId: null,
  title: 'New Column',
  cards: []
};

const columnReducer = (state = initialState, action) => {
  switch (action.type) {
    case BOARD_ACTIONS.ADD_COLUMN: {
      return {
        ...state,
        columnId: action.payload.columnId
      };
    }
    case BOARD_ACTIONS.RENAME_COLUMN: {
      return {
        ...state,
        title: action.payload.title
      };
    }
    case BOARD_ACTIONS.ADD_CARD: {
      return {
        ...state,
        cards: [...state.cards, action.payload.cardId]
      };
    }
    case BOARD_ACTIONS.DELETE_CARD: {
      const nextCards = [...state.cards];
      nextCards.splice(nextCards.indexOf(action.payload.cardId), 1)

      return {
        ...state,
        cards: nextCards
      };
    }
    case BOARD_ACTIONS.MOVE_CARD: {
      const nextCards = [...state.cards];

      if (action.payload.sourceColumnId === state.columnId) {
        nextCards.splice(action.payload.sourceIndex, 1)
      }
      if (action.payload.destinationColumnId === state.columnId) {
        nextCards.splice(action.payload.destinationIndex, 0, action.payload.cardId)
      }

      return {
        ...state,
        cards: nextCards
      };
    }
    default:
      return state;
  }
};

export default columnReducer;



