import { combineReducers, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';

import boardReducer from './reducers/boardReducer/boardReducer';
import columnsReducer from './reducers/columnsReducer/columnsReducer';
import cardsReducer from './reducers/cardsReducer/cardsReducer';

const PERSISTENT_STORAGE_KEY = 'SCOTTY_BOARD_STATE';

const storedState = window.localStorage && window.localStorage.getItem(PERSISTENT_STORAGE_KEY);
const initialState = storedState ? JSON.parse(storedState) || undefined : undefined;

const rootReducer = combineReducers({
  board: boardReducer,
  columns: columnsReducer,
  cards: cardsReducer
});

const store = createStore(rootReducer, initialState, composeWithDevTools());

window.addEventListener('beforeunload', () => {
  window.localStorage.setItem(PERSISTENT_STORAGE_KEY, JSON.stringify(store.getState()))
})

export default store;



